@media (max-width: 767px) {
  .menu-toggle {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 24px;
    height: 14px;
    transition-duration: 1s;
    cursor: pointer;
    border: none;
    padding: 24px;
    background: transparent;
    z-index: 100;
  }
}
.menu-toggle.focus-visible:focus {
  outline: 1px solid #00c8ff;
}

.menu-toggle__line {
  height: 2px;
  width: 24px;
  background: #000000;
  position: absolute;
  left: 11px;
  transition-duration: 0.25s;
  transition-delay: 0.25s;
}
.menu-toggle__line::before {
  content: "";
  left: 0;
  position: absolute;
  top: -6px;
  height: 2px;
  width: 24px;
  background: #000000;
  transition: transform 0.25s, top 0.25s 0.25s;
}
.menu-toggle__line::after {
  content: "";
  left: 0;
  position: absolute;
  top: 6px;
  height: 2px;
  width: 24px;
  background: #000000;
  transition: transform 0.25s, top 0.25s 0.25s;
}

.header__overlay--open .menu-toggle__line {
  transition-duration: 0.1s;
  transition-delay: 0.1s;
  background: transparent;
}

.header__overlay--open .menu-toggle .menu-toggle__line::before {
  transition: top 0.25s, transform 0.45s 0.35s;
  top: 0;
  transform: rotateZ(-225deg);
  background: #000000;
  width: 25px;
}

.header__overlay--open .menu-toggle .menu-toggle__line::after {
  transition: top 0.6s, transform 0.45s 0.35s;
  top: 0;
  transform: rotateZ(225deg);
  background: #000000;
  width: 25px;
}

