  max-width: 350px;
  margin: 0;
}
@media (max-width: 1023px) {
  .product__title {
    font-size: 12px;
    line-height: 17px;
    font-weight: 300;
    color: #000000;
  }
}

.product__price-wrapper {
  display: flex;
}

.product__current-price {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
}

.product__nosale-price {
  font-size: 14px;
  line-height: 17px;
  font-weight: 300;
  color: #aac4e1;
  text-decoration-line: line-through;
}

.product__marker {
  font-size: 11px;
  line-height: 14px;
  font-weight: 400;
  color: #000000;
  text-transform: uppercase;
  background-color: #80bdee;
  padding: 6px 13px;
  position: absolute;
  top: 0;
  right: 0;
}
@media (max-width: 1023px) {
  .product__marker {
    font-size: 10px;
    line-height: 14px;
    font-weight: 400;
    color: #000000;
  }
}

